import React, { useCallback, useEffect, useState } from 'react';
import { connect, useDispatch } from 'react-redux';
import { useLocation, useNavigate } from 'react-router-dom';
import { first, take } from 'rxjs/operators';
import { documentation1, issueIcon, noColleaguesIllustration, reset2 } from '../../../assets/images/icons';
import { setOnBoardingModalsVisibility, setOnBoardingVisible } from '../../../reduxStore/actions/modalsActions';
import { getAllUsers, getFilteredUsers, getUnassignedUsers } from '../../../services/user.service';
import { LeftSideFilter, Spinner, PermissionWrapper, po } from '../../common';
import UserCard from '../../common/UserCard/UserCard';
import Button from '../../common/UI/Button/Button';
import cls from './Company.module.scss';
import UserOverviewModal from '../CompanyComps/UserOverviewModal';
import CreateUsers from '../../OnboardingEntries/OnboardingModals/CreateUsers';
import HeaderTopBar from '../../common/HeaderTopBar/HeaderTopBar';
import { setShowMenu } from '../../../reduxStore/actions/flowActions';
import { APP_NAME } from '../../../shared/utility';

document.title = `Users | ${APP_NAME}`;

const R = require('ramda');
const randomColor = require('randomcolor');

function getRandomInt(min, max) {
	min = Math.ceil(min);
	max = Math.floor(max);
	return Math.floor(Math.random() * (max - min)) + min;
}
const randomNumberString = String(getRandomInt(10, 245));
export const randomisedColor = { color: `rgb(${randomNumberString},${randomNumberString},${randomNumberString})` || randomColor({}) };
const Users = props => {
	const location = useLocation();
	const { user, onToggleModalVisibility, onSetOnBoardingVisible, companyUsersArray, profile = false, companyData } = props;
	const [apiRequest, setApiRequest] = useState(false);
	const [usersArray, setUsersArray] = useState(companyUsersArray);
	const [unassignedUsersArray, setUnassignedUsersArray] = useState([]);
	const [noColleagues, setNoCollegues] = useState(false);
	const [colleagueModalVisible, setColleagueModalVisible] = useState(false);
	const [addUsers, setAddUsers] = useState(false);
	const [activeItem, setActiveItem] = useState('');
	const [filteredIds, setFilteredIds] = useState([]);
	const [filterData, setFilterData] = useState({ filterActive: false, departments_filteredIds: [], positions_filteredIds: [] });
	const [processingResetFilters, setProcessingResetFilters] = useState(false);
	const [loading, setLoading] = useState(false);
	const settingLoading = val => setApiRequest(val);
	const dispatch = useDispatch();
	const navigate = useNavigate();
	useEffect(() => {
		settingLoading(true);
		getUnassignedUsers()
			.pipe(first())
			.subscribe({
				next: data => {
					setUnassignedUsersArray(data);
				},
			});

		dispatch(setShowMenu(profile ? false : true));

		if (companyUsersArray.length == 0) {
			setLoading(true);
			getAllUsers()
				.pipe(take(1))
				.subscribe({
					next: data => {
						if (data.length === 0) {
							setNoCollegues(true);
							setLoading(false);
						} else {
							setUsersArray([...data]);
							setLoading(false);
						}
					},
					error: _error => {
						setLoading(false);
					},
					complete: () => {
						setLoading(false);
					},

				});
		}
	}, []);

	useEffect(() => {
		const item = usersArray?.find(i => i.id == new URLSearchParams(location?.search).get('userId'));
		if (item) {
			openTaskModal(item);
		}
	}, [location?.search, usersArray]);

	useEffect(() => {
		if (filterData.filterActive) {
			const newFilteredIds = [...filterData.departments_filteredIds, ...filterData.positions_filteredIds];
			if (newFilteredIds?.length > 0) {
				setFilteredIds([...newFilteredIds]);
			}
		}
	}, [filterData]);


	const openTaskModal = item => {
		navigate('/user-profile', { state: { user: item } });
	};
	const handleClose = () => {
		setColleagueModalVisible(false);
	};


	const onAddUserClick = () => {
		setAddUsers(true);
	};

	return (
		<div className={cls.usersHost}>
			<div style={{ margin: '100px 0 0 0' }}>
				<HeaderTopBar headerTitle={profile ? 'My Profile' : 'Users'} targetProcessing={false} />
			</div>
			{apiRequest && (
				<span className={cls.loadingIndicator}>
					<Spinner type={'BallTriangle'} color="var(--app-color-gray-dark)" height={30} width={`${30}px`} />
				</span>
			)}
			<br />
			<div style={{ display: profile ? 'none' : 'flex' }}>
				{companyData?.status === 'free' && companyUsersArray?.length < 3 && <Button title={'Add Users'} clicked={onAddUserClick} style={{ width: '150px' }} />}
				{companyData?.status === 'on_trial' && companyUsersArray?.length < 5 && <Button title={'Add Users'} clicked={onAddUserClick} style={{ width: '150px' }} />}
				{companyData?.status === 'active' && <Button title={'Add Users'} clicked={onAddUserClick} style={{ width: '150px' }} />}
				<Button
					light
					clicked={() => {
						setLoading(true);
						getAllUsers(true)
							.pipe(take(1))
							.subscribe({
								next: data => {
									if (data.length === 0) {
										setNoCollegues(true);
										setLoading(false);
									} else {
										setUsersArray([...data]);
										setLoading(false);
									}
								},
								error: _error => {
									setLoading(false);
								},
								complete: () => {
									setLoading(false);
								},
							});
					}}
					icon={<img src={reset2} />}
					loading={loading}
				/>
			</div>
			<br />
			{noColleagues ? (
				<div className={cls.noTeamMembers}>
					<img src={noColleaguesIllustration} alt={''} />
					<p>{'There are currently no team members.'}</p>
					<Button
						title={'Add Users'}
						clicked={() => {
							onToggleModalVisibility({ eightModalVisible: true, hideSkipBtn: true });
							onSetOnBoardingVisible(true);
							window.scrollTo(0, 0);
						}}
					/>
				</div>
			) : (
				<div className={cls.colleaguesSection}>

					<div className={cls.rightSideContainer}>
						{profile ? (
							<div style={{ padding: '10px' }}>
								<UserCard photo={user.photo} firstName={user?.first_name} lastName={user?.last_name} position={user?.phone} email={user?.email} cardClicked={() => { }} />
							</div>
						) : (
							usersArray?.length > 0 &&
							usersArray?.map((item, index) => {
								return (
									<div key={index} style={{ padding: '10px' }}>
										<UserCard
											photo={item.photo}
											firstName={item?.first_name}
											lastName={item?.last_name}
											position={item?.positions?.slice(0, 1)?.map(pos => {
												return pos.name;
											})}
											email={item?.email}
											cardClicked={() => openTaskModal(item)}
										/>
									</div>
								);
							})
						)}
					</div>
				</div>
			)}
			{colleagueModalVisible && <UserOverviewModal isVisible={colleagueModalVisible} activeItem={activeItem} handleClose={handleClose} isAdmin={user.is_admin} />}
			{addUsers && (
				<CreateUsers
					hasTutorial
					modalVisible={addUsers}
					closeButtonAction={() => setAddUsers(false)}
					backButtonAction={() => setAddUsers(false)}
					shadowedContainerStyle={{ minWidth: '1110px', minHeight: '725px' }}
					modalWrapperStyle={{ minWidth: '1110px', minHeight: '725px' }}
					onContinueButtonAction={() => {
						setAddUsers(false);
					}}
					onCancel={() => setAddUsers(false)}
				/>
			)}
		</div>
	);
};

const mapStateToProps = store => {
	const {
		authenticatedUser: { position },
	} = store.authReducer;
	const { user } = store.sessionReducer;

	const { companyData, companyPositionsArray, allTasks, allCompletedTasks, allCompletedTasksPaginated, companyUsersArray } = store.flowReducer;
	return {
		companyData,
		companyPositionsArray,
		position,
		allTasks,
		allCompletedTasks,
		allCompletedTasksPaginated,
		user,
		companyUsersArray,
	};
};

const mapDispatchToProps = dispatch => {
	return {
		onSetOnBoardingVisible: visible => dispatch(setOnBoardingVisible(visible)),
		onToggleModalVisibility: modalVisible => dispatch(setOnBoardingModalsVisibility(modalVisible)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(Users);
