import { useCallback, useEffect, useState } from 'react';
import 'react-datepicker/dist/react-datepicker.css';
import { isMobile } from 'react-device-detect';
import { connect, useDispatch } from 'react-redux';
import Form from 'react-validation/build/form';
import { isEmail } from 'validator';

import { setInviteColleguesList, setMessageBar } from '../../../reduxStore/actions/flowActions';
import { tutorialModalOpen } from '../../../reduxStore/actions/modalsActions';
import { createInvitation, getAllCompanyPositionsAsArray } from '../../../services/company.service';
import { checkUserEmail, createUser } from '../../../services/user.service';
import { timezones } from '../../../shared/utility';
import { Button, ModalR, SelectWithSearch } from '../../common';
import './CreateUsers.scss';
import { take } from 'rxjs';

const R = require('ramda');

const CreateUsers = props => {
	const { closeButtonAction, onContinueButtonAction, onCancel, inviteColleguesList, companyPositionsArray, companyUsersArray } = props;
	const [newCollegue, setNewCollegue] = useState({ companyEmail: '', firstName: '', lastName: '', position: '', position_id: '' });
	const [errors, setErrors] = useState({ companyEmail: '', firstName: '', lastName: '', position: '' });
	const [tzone, setTzone] = useState('Hongkong');
	const [apiError, setAPIError] = useState('');
	const [createLoading, setCreateLoading] = useState(false);
	const [inviteLoading, setInviteLoading] = useState(false);

	const dispatch = useDispatch();
	useEffect(() => {}, [inviteColleguesList]);
	useEffect(() => {
		getAllCompanyPositionsAsArray().subscribe({});
	}, []);

	const checkEmailCompany = (email, createHandler = () => {}) => {
		if (email) {
			setErrors({ ...apiError, companyEmail: `` });
			setInviteLoading(true);
			checkUserEmail({ email: email })
				.pipe(take(1))
				.subscribe({
					next: data => {
						setErrors({ ...apiError, companyEmail: `Email already exists and being used. Please choose another email` });
						setInviteLoading(false);
					},
					error: error => {
						setInviteLoading(false);
						createHandler();
					},
				});
		}
	};
	const createUserInvite = useCallback(() => {
		if (companyUsersArray.filter(user => user.email === newCollegue.companyEmail).length > 0) {
			setErrors(c => ({ ...c, companyEmail: 'Email already exists and being used.' }));
			return;
		}

		setErrors({ companyEmail: '', firstName: '', lastName: '', position: '' });
		setAPIError('');
		var isValid = true;
		if (!isEmail(newCollegue.companyEmail)) {
			isValid = false;
			setErrors(c => ({ ...c, companyEmail: 'Invalid Company email' }));
		}
		if (newCollegue.firstName?.length < 2) {
			isValid = false;
			setErrors(c => ({ ...c, firstName: 'Invalid First name' }));
		}
		if (newCollegue.lastName?.length < 2) {
			isValid = false;
			setErrors(c => ({ ...c, lastName: 'Invalid Last name' }));
		}
		if (newCollegue.position?.length < 2) {
			isValid = false;
			setErrors(c => ({ ...c, position: 'Position id is not considered valid' }));
		}

		if (isValid) {
			const invitationData = {
				email: newCollegue.companyEmail,
				first_name: newCollegue.firstName,
				last_name: newCollegue.lastName,
				position_id: newCollegue.position_id,
			};

			setInviteLoading(true);
			createInvitation(invitationData).subscribe({
				next: data => {
					dispatch(setMessageBar({ show: true, message: `You have successfully created an invitation to ${newCollegue.firstName} `, top: '0' }));

					onContinueButtonAction();
				},
				error: error => {
					if (error) {
						setErrors({
							companyEmail: error?.data?.errors?.email || '',
							firstName: error?.data?.errors?.first_name || '',
							lastName: error?.data?.errors?.last_name || '',
							position: error?.data?.errors?.position_id || '',
						});
					}
					setAPIError(`status ${error?.status}: ${error?.data?.message}`);
					setInviteLoading(false);
				},
				complete: () => setInviteLoading(false),
			});
		}
	}, [newCollegue]);
	const createUserPressed = () => {
		if (companyUsersArray.filter(user => user.email === newCollegue.companyEmail).length > 0) {
			setErrors(c => ({ ...c, companyEmail: 'Email already exists' }));
			return;
		} else if (companyUsersArray.filter(user => user.username === newCollegue.username).length > 0) {
			setErrors(c => ({ ...c, username: 'Username already exists' }));
			return;
		}

		setCreateLoading(true);
		createUser({
			email: newCollegue.companyEmail,
			username: newCollegue.username,
			first_name: newCollegue.firstName,
			last_name: newCollegue.lastName,
			timezone: tzone,
			position_id: [newCollegue.position_id],
		}).subscribe({
			next: data => {
				dispatch(setMessageBar({ show: true, message: `You have successfully created ${newCollegue.firstName}. Default password is "P@ssw0rd"`, top: '0' }));
				onContinueButtonAction();
			},
			error: error => {
				//console.log('user create, [error]', error);
				if (error) {
					setErrors({
						companyEmail: error?.data?.errors?.email || '',
						firstName: error?.data?.errors?.first_name || '',
						lastName: error?.data?.errors?.last_name || '',
						position: error?.data?.errors?.position_id || '',
						username: error?.data?.errors.username || '',
					});
				}
				setAPIError(`status ${error?.status}: ${error?.data?.message}`);
				setCreateLoading(false);
			},
			complete: () => setCreateLoading(false),
		});
	};

	return (
		<div style={{ width: 'inherit', minWidth: '300px' }}>
			<ModalR scrollable={true} shadowedContainerStyle={{ width: isMobile ? '100%' : '30%', minWidth: '450px' }} isOpen={props.modalVisible} modalClosed={onCancel} blured={props.blured}>
				<div
					style={{
						position: 'relative',
						display: 'flex',
						height: '96%',
						flexDirection: 'column',
						justifyContent: 'flex-start',
						alignItems: 'stretch',
						margin: '2% 10% 2% 10%',

						// ...props?.childrenWrapperStyle,
					}}
				>
					<div
						style={{
							margin: '0px 0 0px 0',
							alignSelf: 'stretch',
							display: 'flex',
							flexDirection: 'row',
							width: '100%',
							justifyContent: props.headingCentered ? 'center' : 'flex-start',
							alignItems: props.headingCentered ? 'center' : 'stretch',
						}}
					>
						{props.headingImage && <img alt="" src={props.headingImage} style={{ alignSelf: 'center', height: '30px', width: '32px' }} />}
						<h1
							style={{
								color: '#4A4A4A',
								fontFamily: 'var(--app-text-main-font)',
								fontSize: '2em',
								lineHeight: '2em',
								margin: '0',
								padding: '0 0 0 10px',
								textAlign: 'center',
								width: '100%'
							}}
						>
							{'Add Users'}
						</h1>
					</div>
					<div className={'subheadingContainer_IX'}>
						<p className={'subheading_IX'}>{'Fill the email, name and position of the user youâ€™d like to add. For invites, user will need to accept via email sent'}</p>
					</div>

					<div style={{ minHeight: '500px', display: 'flex', flexDirection: 'column', justifyContent: 'space-between', alignItems: 'stretch' }}>
						<Form>
							<div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', alignItems: 'center' }}>
								<div
									style={{
										marginRight: '10px',
										//padding: '20px',
										width: '100%',
										height: '100%',
										flex: 1,
										display: 'flex',
										flexDirection: 'column',
										justifyContent: 'space-evenly',
										alignItems: 'center',
										boxSizing: 'border-box',
										minHeight: '480px',
										minWidth: '351px',
										//border: '1px solid var(--app-color-gray-lighter)',
										//borderRadius: '8px',
										backgroundColor: '#FFFFFF',
									}}
								>
									<div style={{ margin: '10px 0px 0 0', dispay: 'flex', width: '100%', flexDirection: 'column', justifyContent: 'space-evenly', alignItems: 'center' }}>
										<input
											type="text"
											name={'comapny-name'}
											className={'inputStyle_IX'}
											placeholder="Email"
											value={newCollegue.companyEmail}
											onChange={e => {
												setNewCollegue(c => ({ ...c, companyEmail: e.target.value }));
											}}
											onFocus={() => {
												setErrors(c => ({ ...c, companyEmail: '' }));
											}}
										/>
										{errors?.companyEmail?.length > 0 && <p style={{ margin: '1px 0 1px 2px ', fontFamily: 'var(--app-text-main-font)', fontSize: '14px', color: 'var(--app-color-error)' }}>{errors?.companyEmail}</p>}
									</div>
									{errors?.username?.length > 0 && (
										<div style={{ margin: '10px 0px 0 0', dispay: 'flex', width: '100%', flexDirection: 'column', justifyContent: 'space-evenly', alignItems: 'center' }}>
											<input
												type="text"
												name={'user-name'}
												className={'inputStyle_IX'}
												placeholder="Username"
												value={newCollegue.username}
												onChange={e => {
													setNewCollegue(c => ({ ...c, username: e.target.value }));
												}}
											/>
											{errors?.username?.length > 0 && <p style={{ margin: '1px 0 1px 2px ', fontFamily: 'var(--app-text-main-font)', fontSize: '14px', color: 'var(--app-color-error)' }}>{errors?.username}</p>}
										</div>
									)}
									<div style={{ margin: '10px 0px 0 0', dispay: 'flex', width: '100%', flexDirection: 'column', justifyContent: 'space-evenly', alignItems: 'center' }}>
										<SelectWithSearch
											triggerType={'chevron'}
											molclassname={'menuOverlayList_IX'}
											moliclassname={'menuOverlayListItem_IX'}
											title=""
											placeholder={'Position'}
											onChange={position => {
												if (position) {
													setNewCollegue(c => ({ ...c, position: position.label, position_id: position.value }));
												}
											}}
											items={companyPositionsArray}
										/>
										{errors?.position?.length > 0 && <p style={{ margin: '1px 0 1px 2px ', fontFamily: 'var(--app-text-main-font)', fontSize: '14px', color: 'var(--app-color-error)' }}>{errors?.position}</p>}
									</div>

									<div style={{ margin: '10px 0px 0 0', dispay: 'flex', width: '100%', flexDirection: 'column', justifyContent: 'space-evenly', alignItems: 'center' }}>
										<input
											type="text"
											name={'first-name'}
											className={'inputStyle_IX'}
											placeholder="First Name"
											value={newCollegue.firstName}
											onChange={e => {
												setNewCollegue(c => ({ ...c, firstName: e.target.value }));
											}}
											onFocus={() => {
												setErrors(c => ({ ...c, firstName: '' }));
											}}
										/>
										{errors?.firstName?.length > 0 && <p style={{ margin: '1px 0 1px 2px ', fontFamily: 'var(--app-text-main-font)', fontSize: '14px', color: 'var(--app-color-error)' }}>{errors?.firstName}</p>}
									</div>
									<div style={{ margin: '10px 0px 0 0', dispay: 'flex', width: '100%', flexDirection: 'column', justifyContent: 'space-evenly', alignItems: 'center' }}>
										<input
											type="text"
											name={'last-name'}
											className={'inputStyle_IX'}
											placeholder="Last Name"
											value={newCollegue.lastName}
											onChange={e => {
												setNewCollegue(c => ({ ...c, lastName: e.target.value }));
											}}
											onFocus={() => {
												setErrors(c => ({ ...c, lastName: '' }));
											}}
										/>
										{errors?.lastName?.length > 0 && <p style={{ margin: '1px 0 1px 2px ', fontFamily: 'var(--app-text-main-font)', fontSize: '14px', color: 'var(--app-color-error)' }}>{errors?.lastName}</p>}
									</div>

									<div style={{ margin: '10px 0px 0 0', dispay: 'flex', width: '100%', flexDirection: 'column', justifyContent: 'space-evenly', alignItems: 'center' }}>
										<SelectWithSearch
											inputStyle={{pointerEvents: 'none'}}
											items={timezones}
											defaultSelectedItem={R.find(R.propEq('value', tzone))(timezones)}
											triggerType={'chevron'}
											placeholder={'Select timezone'}
											onChange={selection => {
												setTzone(selection?.value);
											}}
										/>
									</div>
								</div>
							</div>
						</Form>

						<div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', alignSelf: 'center' }}>
							<Button title={'Close'} clicked={closeButtonAction} style={{ width: '100px' }} />
							<Button
								title={'Invite User'}
								light
								disabled={false}
								loading={inviteLoading}
								clicked={() => {
									checkEmailCompany(newCollegue.companyEmail, createUserInvite);
								}}
								style={{ width: '140px' }}
							/>
							<Button title={'Create User'} light disabled={false} loading={createLoading} clicked={() => checkEmailCompany(newCollegue.companyEmail, createUserPressed)} style={{ width: '140px' }} />
						</div>
					</div>
				</div>
			</ModalR>
			
		</div>
	);
};

const mapStateToProps = store => {
	const { inviteColleguesList, companyPositionsArray, companyData, companyUsersArray } = store.flowReducer;
	return { inviteColleguesList, companyPositionsArray, companyData, companyUsersArray };
};
const mapDispatchToProps = dispatch => {
	return {
		onSetInviteColleguesList: arrayColleguesList => dispatch(setInviteColleguesList(arrayColleguesList)),
		onTutorialModalOpen: options => dispatch(tutorialModalOpen(options)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(CreateUsers);
